.trade-hint {
  width: 430px;
  height: 202px;
  position: fixed;
  z-index: 7;
  background-image: url('./img/announcement-bg.png');
  border: 1px solid transparent;
  border-radius: 8px;
  background-size: 100% 100%;
  bottom: 36px;
  right: 48px;
  display: flex;
  color: #FFFFFF;
  .m-check {
    display: none;
  }
  a{
    text-decoration: underline;
  }
  .hint-img {
    padding: 0 24px;
    margin-top: 34px;
    display: flex;
    width: 30px;
    height: 30px;

    img {
      cursor: pointer;
    }
  }

  .hint-box {
    margin-top: 34px;
    margin-left: 15px;
    padding-top: 3px;
    width: 337px;
    height: 128px;

    .hint-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 18px;
      img{
        cursor: pointer;
      }
    }

    .hint-content {
      font-weight: normal;

      .hint-text {
        margin-top: 25px;
        margin-bottom: 26px;
      }

    }

  }
}
.trade-hint:hover{
  border: 1px solid #CD7A37;
}

@media only screen and (max-width: 759px) {
  .trade-hint {
    position: static;
    width: 100%;
    height: 56px;
    min-width: 375px;
    background-image: url('./img/announcement-m.png');

    .close {
      display: none;
    }

    .hint-img {
      padding-left: 24px;
      margin-top: 9px;
      display: flex;
      width: 30px;
      height: 30px;

      img {
        width: 24px;
        height: 24px;
        cursor: pointer;
      }
    }

    .hint-box {
      margin-top: 8px;
      margin-left: 5px;
      width: 100%;
      height: 58px;

      .hint-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 12px;
      }

      .hint-content {
        font-weight: normal;
        font-size: 10px;

        .hint-text {
          margin-top: 0;
          margin-bottom: 0;
        }

        .hint-link {
          display: none;
        }

      }

    }

    .m-check {
      padding:0 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .check {
        display: inline-block;
        width: 52px;
        height: 19px;
        text-align: center;
        line-height: 19px;
        background: #5577FD;
        border-radius: 2px;
        font-size: 12px;
        font-weight: normal;
      }
    }
  }
  .trade-hint:hover{
    border: 1px solid transparent;
  }
}